.highlight {
  background: #db644b;
}

.navigation {
  display: flex;
  justify-content: space-around;
}

.results {
  word-wrap: normal;
  width: auto;

  .text_with_highlight {
    margin-top: 10px;
    margin-bottom: 10px;
    overflow: show;

    .result_matches {
      align-items: center;
      display: flex;
      flex-direction: column;

      .result_matches_warning {
        width: 80%;
        font-size: 22px;
        border-style: solid;
        border-color: red;
        color: red;
        text-align: center;
      }
      .result_matches_text {
        margin-top: 10px;
        width: 80%;
      }
    }

    .result_nomatches {
      .result_nomatches_warning {
        font-size: 22px;
        border-style: solid;
        border-color: green;
        color: green;
      }
      .result_nomatches_text {
        margin-top: 10px;
      }
    }
  }
}

.results_main {
  text-align: center;
}
